.row {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 0.25rem;
}

.element {
  position: relative;
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  cursor: pointer;
  transition: 0.1s all linear;
  z-index: 25;
  background-color: rgb(16, 19, 24);
  /* gap: 0.25rem; */
}

.row_elements_part {
  display: flex;
  flex-direction: row;
  gap: 0.25rem;
}

.element:hover {
  z-index: 100;
  transform: scale(1.45, 1.45);
}

.symbol {
  font-size: 1.6rem;
  font-weight: 400;
}

.full_element_name {
  font-size: 0.55rem;
}

.atomic_number, .atomic_mass {
  font-size: 0.6rem;
}

.atomic_number {
  position: absolute;
  left: 5px;
  top: 5px;
}

.atomic_mass {
  position: absolute;
  right: 5px;
  top: 5px;
}

.row7 {
  margin-bottom: 1.5rem;
}

.nothing {
  visibility: hidden;
  border: 2px solid transparent;
}

.atom_card_container {
  z-index: 100000000;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}

.atom_card{
  min-height: 60vh;
  padding: 3rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  position: relative;
  transition: 0.2s all ease-in-out;
  /* display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center; */
  width: 60vw;
  background-color: rgb(16, 19, 24);
  border: 1px solid rgb(38, 38, 38);
  animation: comeout 0.3s linear;
}

@keyframes comeout {
  0% {
    transform: scale(0.2, 0.2);
  } 
  100% {
    transform: scale(1, 1);
  }
}


.atom_modal {
  position: relative;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  /* height: 100%; */
}
.atom_information {
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
  animation: comeout 0.4s linear;
}
.atom_deep_info { 
  position: absolute;
  top: 20px;
  /* animation: comeout 0.3s linear; */
}

.atom_symbol {
  font-size: 4rem;
  transition: 0.2s all linear;
  /* animation: comeout 0.3s linear; */
}

.closebtn {
  position: absolute;
  left: 10px;
  top: 10px;
  height: 30px;
  width: 30px;
  cursor: pointer;
}

.orbit{
  width:330px;
  height:330px;
  border:1px solid #CCC;
  position:absolute;
  top:0; bottom:0; left:0;right:0;
  margin:auto;
  border-radius:50%; 
}

.atom_nameandsymbol {
  text-align: center;
}

.sort_nav {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  background-color: rgb(16, 19, 24);
  /* border-bottom: 1px solid rgba(55, 55, 55, 0.392); */
  justify-content: center;
  gap: 2rem;
}

.sort_nav > p{
  cursor: pointer;
  padding: 0.5rem 0;
}

.pertable_audio {
  position: absolute;
  left: 0;
  bottom: 0;
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  gap: 1rem;
  padding: 0 1rem 1rem;
}

audio {
  /* background: transparent; */
  border-radius: 6rem;
  /* background: gradi; */
}

.fortouch {
  display: none;
}
.openclose {
  display: none;
}

.downbtn, .upbtn { 
  display: none;
}


