    z-index: 10000;
    gap: 1rem;
    transition: 0.3s all linear;
    height: fit-content;
    height:  100%;
    text-align: center;
    background-color: rgb(16, 19, 24);
    transform: translateX(-100%);
  }
  .sort_nav.shown {
    transform: translateX(0%);
