    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .element {
    /* transform: translateY(-200px); */
    width: 98vw;
    /* min-height: 25vh; */
    height: auto;
    aspect-ratio: 1/1;
    /* padding: 5rem 0; */
    margin-bottom: 1rem;
    position: relative;
  }
  .symbol {
    font-size: 3rem;
  }
  .full_element_name {
    font-size: 1.2rem;
  }
  .element:hover {
