  }

  .row7, .row8 {
    /* margin-bottom: -22.5rem; */
  }
  .atom_card_container {
    display: none;
  }
  html, body, .periodictable, * {
    scrollbar-width: 0;
  }
  .atom_modal {
    position: absolute;
    z-index: 1;
    height: 100%;
    width: 100%;
    z-index: -1;
    top: 0;
    opacity: 0.25;
  }
  .atomic_mass, .atomic_number {
    font-size: 1rem;
  }
  .electronic_configuration > h5 {
    font-size: 2rem;
    position: absolute;
    bottom: 0;
    left: 10px;
  }
